@media screen and (max-width: 400px) {
  .contact_descr {
    font-size: 15px !important; }
  .contact_social_icon_div i {
    font-size: 35px !important; } }

input:focus {
  outline: none; }

textarea:focus {
  outline: none; }

body {
  overflow-x: hidden;
  margin: 0; }

.contact_container {
  display: block;
  min-height: 100vh;
  position: relative;
  padding-bottom: 30px; }

.contact_bg {
  display: block;
  position: relative;
  min-height: 550px;
  margin-bottom: 20px;
  background-image: url("../images/contact/contact_bg.png");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat; }

.contact_first_half {
  position: relative;
  display: block;
  top: 0;
  left: 0;
  padding-top: 15px;
  padding-bottom: 30px;
  min-height: 650px;
  font-family: "Big Shoulders Text", cursive; }

.menu_nav_link {
  display: block;
  width: 200px;
  height: 25px;
  position: relative;
  margin-bottom: 20px;
  padding: 0 5px 5px 20px;
  letter-spacing: 0.5px;
  font-size: 17px; }

.contact_title {
  font-size: 50px;
  margin: 20px 10px 10px 10px;
  font-weight: bold;
  position: relative; }

.contact_title:after {
  content: '';
  width: 100px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba(203, 48, 241, 0.6); }

.contact_descr {
  padding: 0 10px 10px 35px;
  font-size: 20px;
  margin-top: 20px; }

.contact_lpa {
  list-style-type: none;
  margin: 50px 5px 0 0;
  font-size: 15px;
  position: relative; }

.contact_lpa li {
  margin-top: 20px; }

.contact_lpa i {
  margin-right: 10px;
  font-size: 22px !important; }

.contact_social_icon_div {
  display: inline-block;
  position: relative;
  min-height: 60px;
  margin-top: 40px;
  padding-left: 25px; }

.contact_social_icon_div i {
  display: block;
  cursor: pointer;
  display: inline-block;
  font-size: 40px;
  margin: 5px; }

.contact_social_icon_div i:hover {
  opacity: 0.9; }

.contact_social_icon_div i:nth-child(1) {
  color: #4460A1; }

.contact_social_icon_div i:nth-child(2) {
  color: #E1533D; }

.contact_social_icon_div i:nth-child(3) {
  color: #5AB1F1; }

.contact_social_icon_div i:nth-child(4) {
  color: #E1533D; }

.contact_social_icon_div i:nth-child(5) {
  color: #095C91; }

.contact_form {
  display: block;
  position: relative;
  min-height: 300px;
  padding-bottom: 40px;
  margin-bottom: 20px;
  text-align: center;
  font-family: "Big Shoulders Text", cursive;
  padding-top: 20px; }

.send_us_message_txt {
  font-size: 45px;
  font-weight: bold; }

.send_us_message_txt span {
  position: relative;
  height: 2.5px;
  width: 90px;
  background-color: rgba(203, 48, 241, 0.8);
  left: 5px;
  bottom: 1px;
  display: block; }

.send_us_message_untxt {
  font-size: 20px;
  padding: 10px; }

.contact_form_wrap {
  display: block;
  min-height: 150px;
  margin-top: 20px; }

.input_half {
  display: block;
  min-height: 150px; }

.input_half span {
  display: block;
  height: 40px;
  width: 80%;
  border-bottom: 2px solid #884276;
  margin: 15px 0 20px 0;
  overflow: hidden; }

.input_half span input {
  border: 0;
  padding: 5px 10px 5px 10px;
  width: 100%;
  height: 40px;
  font-size: 18px; }

.contact_textarea {
  border-bottom: 2px solid #884276;
  height: 100px;
  font-size: 18px;
  overflow: hidden;
  margin-top: 25px; }

.contact_textarea textarea {
  width: 100%;
  height: 100px;
  resize: none;
  border: 0;
  padding: 10px; }

.cont_submit_message {
  display: block;
  width: 150px;
  height: 40px;
  margin-top: 30px;
  border: 2px solid transparent;
  text-align: center;
  padding-top: 7px;
  letter-spacing: 1px;
  font-family: "Big Shoulders Text", cursive;
  text-transform: uppercase;
  background-color: #421F39;
  cursor: pointer;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  color: #fff; }

.cont_submit_message:hover {
  background-color: #7C1762;
  border: 2px solid #421F39; }

.fill_fields_msg {
  padding: 10px;
  color: #FF2222; }
