body {
  overflow-x: hidden;
  margin: 0; }

.footer_container {
  display: block;
  min-height: 250px;
  position: absolute;
  padding-top: 20px;
  left: 0;
  font-family: "Big Shoulders Text", cursive;
  background-color: #1c192b;
  color: white;
  overflow-x: hidden;
  margin: 50px 0 0 0; }

.wrap_footer {
  display: block;
  min-height: 250px; }

.footer_half {
  display: block;
  min-height: 250px; }

.footer_logo {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  min-height: 70px;
  padding: 10px;
  overflow: hidden; }

.footer_logo span:nth-child(1) {
  display: block;
  width: 50px;
  height: 50px;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  border: 2px solid #5B1C51;
  margin: 10px 10px 10px 30px; }

.footer_logo span:nth-child(2) {
  font-size: 25px;
  font-weight: bold;
  margin: 35px 0 0 8px; }

.footer_fh_txt {
  display: block;
  min-height: 120px;
  padding: 30px 7px 10px 20px; }

.foot_ht_half {
  display: block;
  min-height: 240px; }

.footer_half_two ul {
  list-style-type: none;
  padding: 0;
  font-size: 18px; }

.footer_half_two ul li {
  margin-top: 5px;
  cursor: pointer; }

.footer_half_two ul li a {
  color: #fff;
  text-decoration: none; }

.footer_half_two ul li a:hover {
  color: #5B1C51; }

.footer_title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 25px;
  border-bottom: 1.5px solid #fff;
  padding-right: 20px; }

.foot_ht_half div:nth-child(1) {
  padding: 20px 0 0 20px; }

.foot_ht_half div:nth-child(2) {
  padding: 0 0 0 20px; }

.foot_ht_h_ul {
  margin-top: 50px;
  font-size: 18px; }

.foot_ht_h_ul li:hover {
  color: #5B1C51; }

/* ----------- Footer 3rd half ------------- */
.foot_s_half {
  padding: 20px 0 0 0; }

.news_foot_title {
  margin-left: 35px; }

.newsletter_div {
  display: block;
  height: 40px;
  width: 250px;
  overflow: hidden; }

.newsletter_div input {
  width: 100%;
  height: 100%;
  border: 0;
  padding: 0 10px 0 10px; }

.newsletter_button {
  display: block;
  border: 1px solid transparent;
  width: 100px;
  height: 35px;
  margin-top: 15px;
  text-align: center;
  padding-top: 7px;
  letter-spacing: 1px;
  font-size: 14px;
  background-color: #5B1C51;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s; }

.newsletter_button:hover {
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #fff; }

.footer_social_div {
  display: block;
  min-height: 40px;
  margin-top: 20px; }

.footer_social_div i {
  display: block;
  font-size: 25px;
  display: block;
  margin: 5px;
  opacity: 0.8;
  -webkit-transition: 0.1s;
  -o-transition: 0.1s;
  transition: 0.1s;
  cursor: pointer; }

.footer_social_div i:hover {
  opacity: 1; }

.footer_allrights {
  display: block;
  position: relative;
  min-height: 30px;
  background-color: #fff;
  text-align: center;
  color: #000;
  padding: 5px;
  font-size: 12px; }
